.Separator {
    background: -webkit-gradient(linear, left top, right bottom, from(black), to(#222));
    background: -webkit-linear-gradient(top left, black, #222);
    background: -o-linear-gradient(top left, black, #222);
    background: linear-gradient(to bottom right, black, #222);
}

.Separator-title {
    text-align: center;
    color: white;
    font-weight: 300;
    font-size: 4rem;
}

.Separator-subtitle {
    text-align: center;
    color: #efefef;
    font-weight: 300;
    font-family: "Open Sans", sans-serif;
}

.Separator-container {
    position: relative;
    height: 100%;
}

.Separator-textContainer {
    position: absolute;
    top: 50%;
    -webkit-transform: translateY(-60%);
        -ms-transform: translateY(-60%);
            transform: translateY(-60%);
    width: 100%;
}



