input, textarea {
    width: 100%;
    height: 100%;
    background-color: transparent;
    border: none;
    font-size: 22px;
    /*
    position: absolute;
    display: -webkit-box;
    */

    margin-top: 8px;
    margin-bottom: 8px;
    padding-left: 0px !important;
}

textarea {
    height: 150px;
    resize: none;
}

textarea:focus {
    outline: none;
}

.col {
    margin-bottom: 15px;
}

.input-label {
    position: absolute;
    /*font-size: 12px;*/
    -webkit-transition: all .25s; /* Safari */
    -o-transition: all .25s;
    transition: all .25s;
    font-size: 18px;
    pointer-events: none;
    height: 30px;
    line-height: 30px;
}

.input-label.not-focused {
    -webkit-transform-origin: top left;
        -ms-transform-origin: top left;
            transform-origin: top left;
    margin-top: 0px;
    margin: 8px 0px;
}

.input-label.focused {
    -webkit-transform: scale(.65);
        -ms-transform: scale(.65);
            transform: scale(.65);
    -webkit-transform-origin: top left;
        -ms-transform-origin: top left;
            transform-origin: top left;
    margin-top: -10px;
}

.input-block {
    position: relative;
    -webkit-transition: background-color .25s; /* Safari */
    -o-transition: background-color .25s;
    transition: background-color .25s;
    padding: 10px;
    border-radius: 3px;
}

.input-block.focused {
    background-color: rgba(255, 255, 255, .9);
}

.input-block.not-focused {
    background-color: rgba(255, 255, 255, 0.5);
}



